import React, { Component } from 'react';
import CurrentWeather from './components/currentweather';
import DetailedWeather from './components/detailedweather';

export default class Main extends Component {
	state = {
		initialState: {
		},
		status: false
	};

	componentDidMount() {
		this.getWeather();
	}

	getWeather = (key = 324505) => {

		fetch(`https://dataservice.accuweather.com/currentconditions/v1/${key}?apikey=JgEbWxgFwhWVfUfVArF9AH6sl6E1VlNN&language=uk-ua&details=true`)
		.then((res) => {
			return res.json()
		}).then((res) => {
			console.log('res', res[0]);
			const { 
				LocalObservationDateTime: time,
				Temperature: {Metric: temperature},
				RealFeelTemperature: {Metric: realfeel},
				WeatherText: weathertext,
				WeatherIcon: icon,
				PrecipitationSummary: {Precipitation: {Metric: precipitation}},
				Pressure: {Metric: pressure},
				DewPoint: {Metric: dewpoint},
				RelativeHumidity: humidity,
				Visibility: {Metric: visibility},
				Wind: {Direction: winddirection},
				Wind: {Speed: {Metric: windspeed}},
				CloudCover: cloudcover,
				UVIndex: uvindex,
				UVIndexText: uvindextext
			} = res[0];

			this.setState({
				status: true,
				initialState: {
					time,
					temperature,
					realfeel,
					weathertext,
					icon,
					precipitation,
					pressure,
					dewpoint,
					humidity,
					visibility,
					winddirection,
					windspeed,
					cloudcover,
					uvindex,
					uvindextext
				}
			})

		}).catch((err) => {
			console.log(err);
		})
	}

	weatherByCityKey = (key) => {
		this.getWeather(key);
	}

	render() {
		const { initialState: { temperature, time, icon }, status } = this.state;
		return(
			<React.Fragment>
				{ status && 
					<main className='main' role='main'>
						<CurrentWeather weatherByCityKey={this.weatherByCityKey} time={time} temperature={temperature} icon={icon} />
						<DetailedWeather {...this.state.initialState}  />
					</main>
				}
			</React.Fragment>
		)
	}
}